* {
  margin: 0 ;
  padding: 0;
  border: 0;
 box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

html,
body {
  font-family: "Helvetica Neue", Helvetica, 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.content {
  padding-top: 5.2rem;
}

p {
  font-size: 1.6rem;
}


a {
  color: $color-astral;
  text-decoration: none;
  
  &:hover {
      text-decoration: underline;
  }
}

hr {
  border-top: 0.1rem solid $color-gallery;
  margin: 1.5rem;
}

.text-justify {
  text-align: justify;
  p {
      padding: 1rem 0;
  }
}

.youtube-video-16-by-9 {
  width: 100%;
  padding-top: 56.65%;
  position: relative;

  iframe {
      position: absolute;
      top: 0;
      left: 0;
      // z-index: -1;
  }
}

.image img {
    max-width: 100%;
}

.list-padded {
    padding: 1rem 3rem;
    font-size: 1.4rem;
  }
  
  h3 {
    font-size: 1.4rem;
}
@import 'landing-content';